.contact {
    background-image: url("../web/image/img_footer/img.jpg");
    background-size: cover;
    background-position: center ;
    min-height: 866px;

    &__title {
        text-align: center;
        padding-top: 100px;
        font-family: 'Raleway', sans-serif;

        &--title {
            margin: 0 auto;
            font-size: 90px;
            font-weight: 700;
        }

        &--text {
            margin: 45px auto;
            margin-top: 35px;
            font-size: 17px;
            font-weight: 300;
            max-width: 350px;
            min-height: 45px;
        }
        
    }

    &__form {
        float: left;
        margin-left: 11%;
        margin-right: 170px;
        font-family: 'Raleway', sans-serif;
        font-weight: 300;
        font-size: 15px;
        color: #fff;
        min-width: 310px;
    }
}

.contact__list {
    list-style-type:none;
    margin-top: 0;
min-width: 310px;
    .contact__item {
        padding-bottom: 15px;

        input {
            background-color: #00c4c45d;
            border: transparent;
            width: 500px;
            min-width: 310px;
            height: 50px;
            color: #fff;
            padding-left: 10px;

            &:hover {
                box-sizing: border-box;
                border-left: 5px solid #355b82;
                background-color: #00c4c4;
                font-weight: 700;
                height: 50px; 
                width: 500px;
            }

            &::placeholder {
                color: #fff;
            }
        }

        textarea {
            height: 250px;
            width: 500px;
            background-color:#00c4c45d ;
            color: #fff;
            padding-left: 10px;
            resize: none;

            &:hover {
                box-sizing: border-box;
                border-left: 5px solid #355b82;
                background-color: #00c4c4;
                font-weight: 700;
                width: 500px;
                height: 250px;
            }

            &::placeholder {
                color: #ffffff;
            }
        }
    }
}

.contact__button {
    width: 200px;
    height: 50px;
    background-color: #00c4c4;
    color: #fff;
    border: transparent;
    font-family: 'Pacifico', cursive;
    font-size: 25px;

    &:hover {
        transform: translate(8px, 5px);
        text-shadow: 4px 4px 5px #262626;
        box-shadow: -4px -4px 15px #262626;
        cursor: pointer;
    }
}

.contact__contacts {
    margin-right: 11%;
    margin-left:170px;
    padding-bottom: 450px ;
    font-family: 'Raleway', sans-serif;

    //

    %h2 {
        margin: 0 auto;
        margin-bottom: 5px;
        font-size: 17px;
        font-weight: 700;
    }

    h2 {
        margin: 0 auto;
        margin-bottom: 5px;
        font-size: 17px;
        font-weight: 700;
    }

    p{
        @extend %h2;
        margin-bottom: 20px;
        font-size: 20px;
        font-weight: 300;
    }
}

footer {
    background-color: #262626;
    font-family: 'Raleway', sans-serif;
    color: #b9b9b9;
    min-height: 90px;
    display: flex;
    align-items: center;
    justify-content:space-around;

    .footer__copy{
        font-size: 16px;
        font-weight: 300;
    }

    .footer__logo{
        font-family: 'Pacifico', cursive;
        font-size: 25px;
    }

    .footer__author{
        font-size: 15px;
        font-weight: 300;

        .red {color: red;}
    }
}
 

