$font-family:'Raleway', sans-serif;

.subscribe {
    min-height: 500px;
    background-image: url(../web/image/img_subscribe/subscribe.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    font-family: $font-family;
}

.subscribe__title{
    min-width: 115px;
    max-width: 550px;
    margin-left: 11%;
    margin-bottom: 15px;
    padding-top: 145px;
    h2 {
        font-size: 40px;
        margin-bottom: 15px;
    }

    p{
        font-size: 17px;
        min-height: 50px;
    }
}

.subscribe__input{
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    align-items: center;
    margin-left: 11%;


    input {
        width: 570px;
        min-width: 315px;
        height: 55px;
        border: 1px solid #000;
        padding-left: 10px;
        margin-right: 30px;
        margin-bottom: 15px;
    }
    
    button {
        height: 55px;
        width: 170px;
        background-color: #96b92c;
        color: #fff;
        border: transparent;
        box-shadow: 0px 2px 8px  rgb(58, 58, 58);
        margin-bottom: 15px;
    }
}