* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
    transition: all 0.2s; 
}


/* Header */

.navigation{
    &--margin {
    margin: 0px;
    position: fixed;
    height: 117px;
    top: 0;
    z-index: 2;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.35);}
}

.navigation__logo--style {
    margin-left: 11%;
    margin-top: 45px;
    width: 127px;
    margin-bottom: 0px;
    font-family: 'Pacifico', cursive;
    font-size: 50px;
    float: left;
}


.navigation-list{

    &--styling {
    font-size: 0;
    list-style-type: none;
    margin-top: 65px;
    padding-left: 0;
    margin-right: 11%;
    float: right;}

    &__item {
    display: inline-block;
    font-size: 16px;
    font-size: 16px;
    font-weight: 700;}

    &__item--margin {margin-left: 37px;}
}   


.navigation__link {
    text-decoration: none;
    color: #000;
    padding-top: 6px;
    &:hover {
    border-top: 3px solid #ff6033;
    cursor: pointer;
    }
}

.FreshLife {
    background-image: url("../web/image/img_header/strawberry.jpg");
    background-size: cover;
    background-position: center;
    min-height: 1000px;
    font-family: 'Raleway', sans-serif;
    position: relative;
    margin-top: -50px;
    .scroll {
        text-align: center;
        position: absolute;
        bottom: 60px;
        left: 0;
        right: 0;
    }
}

.FreshLife-block__title {
    margin-left: 11%;
    padding-top: 360px;
    margin-bottom: 0;
    font-size: 60px;
    font-weight: 900;
    max-width: 290px;
}

.FreshLife-block__text {
    max-width: 375px;
    min-height: 75px;
    margin-left: 11%;
    margin-top: 31px;
    margin-bottom: 0;
    font-size: 17px;
    font-weight: 300;
}

.FreshLife__button{
    width: 200px;
    height: 55px;
    background-color: #ff6033;
    border: #ff6033;
    color: #fff;
    margin-left: 11%;
    margin-top: 30px;
    font-size: 20px;
    font-weight: 300;
    box-shadow: 0 2px 5px  rgb(46, 46, 46);

    &:hover {
    cursor: pointer;
    transform: rotateX(360deg);
    transition-duration:  1s;
    background-color: green;
    border: green;
    }
}

.AboutUs {
    background-image: url("../web/image/img_header/backgr-blue.png");
    background-size: cover;
    background-repeat: repeat-y;
    min-height: 700px;
    font-family: 'Raleway', sans-serif;
    color: #fff;

    &-block {
    margin-right: 11% ;
    max-width: 503px;
    float: right;
    margin-top:170px ;

        &__title {
        margin: 0;
        font-size: 90px;
        font-weight: 700; 
        }

        &__text {
            font-size: 17px;
            font-weight: 300;
            max-width: 483px;
            margin: 20px 0;

                &__text--width {
                    max-width: 503px;
        }
    }

    &__photo--position {
    margin-top: 0px;
    margin-left: 3%; 
    padding-top: 30px;
    max-width: 650px;
    min-width: 320px;
    width: 50%;
        img{
        max-width: 650px;
        min-width: 320px;
        }
}
}}
