$font-family:'Raleway', sans-serif;

.gallery {
    min-height: 1530px;
    background-color: #ffe92f;
    font-family: $font-family;
    text-align: center;

    &__title {
        min-height:  330px;
        color: #000;
        &--title {
            font-size: 90px;
            font-weight: 700;
            padding-top: 100px;
            padding-bottom: 25px;
        }
        &--text {
            font-size: 17px;
            font-weight: 300;
            max-width: 580px;
            margin: 0 auto;
        }
    }
    button {
        width: 200px;
        height: 50px;
        background-color: #5585a2;
        color: #fff;
        border: transparent;
        font-size: 20px;
        font-weight: 300;
        margin-bottom: 50px;
    }
}

.gallery__container {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;

    img {
        margin: 10px;
        max-width: 100%;
    }

    &top {
        display: flex;
        flex-flow: inherit;
        justify-content: center;
       

        &--small {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
        }
    }

    &bottom {
       margin-bottom: 20px;
    }

   
}